.eco-house {
  position: relative;
  padding-top: 120px;
  padding-bottom: 120px;
  margin-bottom: 350px;
  background-image: url('./../img/forest-bg.png');
  background-position: center center;
  background-size: 100% auto;
  background-repeat: no-repeat;
  @media (min-width: 1440px){
      padding-top: 12rem;
      padding-bottom: 12rem;
      margin-bottom: 35rem;
  }
  &__content {
    position: relative;
    overflow: hidden;
  }
  &__header {
    text-align: center;
    max-width: 768px;
    margin: 0 auto;
    margin-bottom: 130px;
    @media (min-width: 1440px){
        max-width: 76.8rem;
        margin-bottom: 13rem;
    }
  }
  &__title {
    margin-bottom: 15px;
    @media (min-width: 1440px){
        margin-bottom: 1.5rem;
    }
  }
  &__desc {
    margin: 0;
    color: var(--black-brand-color);
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0.32px;
    font-size: 16px;
    @media (min-width: 1440px){
      font-size: 1.6rem;
    }
  }

  &__content {
    margin-top: 130px;
    @media (min-width: 1440px){
        margin-top: 13rem;
    }
  }
  // &__bottom {
  //   position: absolute;
  //   left: 0;
